import axios from 'axios';

export function isScrollable() {
    //   
    const documentHeight = document.documentElement.scrollHeight;
    //  
    const windowHeight = window.innerHeight;

    //         
    return documentHeight > windowHeight;
}


export function modalStyles(type : string){
    const mobi_clime = {
        overlay: {
            backgroundColor: "rgba(0,0,0,0.5)",
            zIndex : 100
        },
        content: {
            backgroundColor : "white",
            left: "50%",
            margin: "auto",
            width: "calc(100% - 5%)",
            height: "calc(100vh - 10vh)",
            padding: "0",
            overflow: "hidden",
            transform : "translate(-50%,0)"
        },
    };
    const clime = {
        overlay: {
            backgroundColor: "rgba(0,0,0,0.5)",
            zIndex : 100
        },
        content: {
            backgroundColor : "white",
            left: '0',
            inset : '0',
            margin: "auto",
            width: "calc(100% - 4%)",
            height: "calc(100vh - 5vh)",
            padding: "0",
            overflow: "hidden",
            //transform : "translate(-50%,0)"
        },
    };
    const mobi_edit = {
        overlay: {
            backgroundColor: "rgba(0,0,0,0.5)",
            zIndex : 100
        },
        content: {
            backgroundColor : "rgb(238,238,238)",
            left: "50%",
            margin: "auto",
            width: "calc(100% - 5%)",
            height: "calc(100vh - 5vh)",
            padding: "0",
            overflowY: "auto",
            transform : "translate(-50%,0)"
        },
    };
    const edit = {
        overlay: {
            backgroundColor: "rgba(0,0,0,0.5)",
            zIndex : 100
        },
        content: {
            backgroundColor : "#eeeeee",
            left: '0',
            margin: "auto",
            width: "460px",
            height: "460px",
            padding: "0",
            overflow: "hidden",
            //transform : "translate(-50%,0)"
        },
    };
    const mobi_manage = {
        overlay: {
            backgroundColor: "rgba(0,0,0,0.5)",
            zIndex : 100
        },
        content: {
            backgroundColor : "rgb(238,238,238)",
            left: "50%",
            margin: "auto",
            width: "360px",
            height: "calc(100vh - 40%)",
            padding: "0",
            overflow: "auto",
            transform : "translate(-50%,0)"
        },
    };
    const manage = {
        overlay: {
            backgroundColor: "rgba(0,0,0,0.5)",
            zIndex : 100
        },
        content: {
            backgroundColor : "white",
            left: '0',
            margin: "auto",
            width: "460px",
            height: "470px",
            padding: "0",
            overflow: "hidden",
            //transform : "translate(-50%,0)"
        },
    };
    let modalStyle = {
        mobi : {},
        web : {}
    };
    if(type === 'clim'){
        modalStyle.mobi = mobi_clime;
        modalStyle.web = clime;
    }
    if(type === 'manage'){
        modalStyle.mobi = mobi_manage;
        modalStyle.web = manage;
    }
    if(type === 'edit'){
        modalStyle.mobi = mobi_edit;
        modalStyle.web = edit;
    }
    return modalStyle;
}



// PDF   
export function downloadPdf(pdfUrl : any, type : string){
    // PDF  URL
    // axios  PDF  
    axios.get(pdfUrl, { responseType: 'blob' })
        .then(response => {
            //   blob 
            const blob = new Blob([response.data]);

            // blob URL 
            const url = window.URL.createObjectURL(blob);

            // a   
            const a = document.createElement('a');
            a.href = url;
            if(type === 'regi') a.download = '.pdf';
            if(type === 'pdf') a.download = '.pdf';
            document.body.appendChild(a);
            a.click();

            //   URL 
            window.URL.revokeObjectURL(url);
        })
        .catch(error => {
            console.error('PDF    :', error);
        });
};

